import jQuery from "jquery/dist/jquery.slim";
import "bootstrap/js/dist/util";
import "bootstrap/js/dist/modal";
import "bootstrap/js/dist/collapse";

var mn = jQuery("#main-nav");
var shrinkNavbar = function() {
  mn.offset().top > 100 ? mn.addClass("navbar-shrink") : mn.removeClass("navbar-shrink");
};
jQuery(window).on('scroll', shrinkNavbar);
jQuery(window).on('load', function() {
  correctFaqHeight('#faq-checkboxes input:checked + label');
});

function correctFaqHeight(labelSelector) {
  // wait for toggle
  setTimeout(function() {
    var faqCheckboxes = jQuery('#faq-checkboxes');
    faqCheckboxes.height('auto');
    var answerHeight = jQuery(`${labelSelector} + p`).height();
    faqCheckboxes.height(answerHeight > faqCheckboxes.height() ? answerHeight : 'auto');
  }, 0);
}

window.scrollToFaq = function(labelSelector) {
  if (window.outerWidth > 991) { 
    // correct height because of height
    correctFaqHeight(labelSelector);
    return;
  }

  // wait for toggle
  setTimeout(function() {
    jQuery('html, body').scrollTop(jQuery(labelSelector).offset().top - 50);
  }, 0);
};

function is_valid_datalist_value(idDataList, inputValue) {
  var option = document.querySelector("#" + idDataList + " option[value='" + inputValue + "']");
  if (option != null) {
    return option.value.length > 0;
  }
  return false;
}

window.doGenerateLinkFormValidate = function() {
  if (is_valid_datalist_value('currencyOptions', document.getElementById('currency').value)) {
    jQuery('#currency').removeClass('is-invalid');
  } else {
    jQuery('#currency').addClass('is-invalid');
  }
};

if(currency) {
  currency.onfocus = function () {
    currencyOptions.style.display = 'block';
  };
  
  for (let option of currencyOptions.options) {
    option.onclick = function () {
      currency.value = this.value;
      currencyOptions.style.display = 'none';
    };
  }
  
  currencyOptions.style.width = currency.offsetWidth + 'px';
  currencyOptions.style.left = currency.offsetLeft + 'px';
  currencyOptions.style.top = currency.offsetTop + currency.offsetHeight + 'px';
}